.about-me-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px 0 0 0;
    font-size: 20px;
}

.about-me-parent {
    max-width: 1000px;
    width: 70%;
}

.about-me-card {
    display: flex;
    width: 100%;
    box-shadow: 0 0px 20px -2px #1f2235;
}

.about-me-profile {
    width: 50%;
    display: flex;
    margin-top: 30px;
    justify-content: center;
}

.about-me-profile img {
    width: 70%;
    height: fit-content;

}

.about-me-details {
    width: 60%;
    text-align: justify;
    padding: 30px;
}

.about-me-description {
    font-size: 18px;

}

.about-me-highlights {
    margin: 50px 0;
}

.about-me-heading {
    font-family: "Poppins Bold";
    font-size: 14px;
    margin: 0 0 5px 0;
}

.highlight {
    display: flex;
    align-items: center;
    margin: 5px 0;
    font-size: 18px;
}

.highlight-blob {
    height: 12px;
    width: 12px;
    margin: 0 10px 0 0;
    background-color: #ff5823;
    border-radius: 50%;
}

.about-me-options .btn {
    width: 40%;
}

.about-me-options .highlighted-btn {
    margin-left: 30px;
}

@media only screen and (max-width: 1110px) {
    .about-me-parent {
        width: 88%;
    }
}

@media only screen and (max-width: 898px) {
    .about-me-profile {
        display: none;
    }
    .about-me-details {
        width: 100%;
    }
}

@media only screen and (max-width: 466px) {
    .about-me-options {
        display: flex;
        flex-direction: column;
    }
    .about-me-options .highlighted-btn {
        margin: 24px 0 0 0 !important;
    }
    .about-me-options .btn {
        width: 100%;
    }
}